* { box-sizing: border-box; }

body {
  margin: 0;
  padding: 0;
  background-color: hsl(0, 0%, 80%);
}


.calculator {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  width: 600px;
  height: 490px;
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-template-rows: repeat(7, 1fr);
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace;
  border: 3px solid #38382e;
  box-shadow: 5px 5px 10px gray; }

.calculator h1 {
  margin: 0;
  padding: 0 5px;
  border: 2px solid #38382e;
  /* for center align text :( */
  display: flex;
  align-items: center;
  justify-content: flex-end; }

.calculator div {
  font-size: 40px;
  text-align: center;
  border: 1px solid #38382e;
  background-color: green;
  /* for center align text :( */
  display: flex;
  align-items: center;
  justify-content: center; }

#formula {
  grid-area: 1 / 1 / span 1 / span 4;
  background-color: #bfbfbf; }

#display {
  grid-area: 2 / 1 / span 1 / span 4;
  background-color: #999999; }

#add {
  grid-area: 3 / 1 / span 1 / span 1;
  background-color: #cc6600; }

#subtract {
  grid-area: 3 / 2 / span 1 / span 1;
  background-color: #cc6600; }

#multiply {
  grid-area: 3 / 3 / span 1 / span 1;
  background-color: #cc6600; }

#divide {
  grid-area: 3 / 4 / span 1 / span 1;
  background-color: #cc6600; }

#seven {
  grid-area: 4 / 1 / span 1 / span 1; }

#eight {
  grid-area: 4 / 2 / span 1 / span 1; }

#nine {
  grid-area: 4 / 3 / span 1 / span 1; }

#four {
  grid-area: 5 / 1 / span 1 / span 1; }

#five {
  grid-area: 5 / 2 / span 1 / span 1; }

#six {
  grid-area: 5 / 3 / span 1 / span 1; }

#three {
  grid-area: 6 / 1 / span 1 / span 1; }

#two {
  grid-area: 6 / 2 / span 1 / span 1; }

#one {
  grid-area: 6 / 3 / span 1 / span 1; }

#zero {
  grid-area: 7 / 1 / span 1 / span 2; }

#decimal {
  grid-area: 7 / 3 / span 1 / span 1; }

#clear {
  grid-area: 4 / 4 / span 2 / span 1;
  background-color: #cc0000; }

#equals {
  grid-area: 6 / 4 / span 2 / span 1;
  background-color: #0000b3; }

